.card-wrapper {
  padding: 8px;
}

.main-profile-card {
  width: 35vw;
  background-color: rgba(255, 255, 255, 0.5) !important;
}

.my-name-mobile {
  font-size: 15vw !important;
  font-family: din-condensed-web;
  font-weight: bold !important;
}

.my-nickname-mobile {
  font-size: 8vw !important;
  font-family: din-condensed-web;
}

.my-name {
  font-size: 6vw !important;
}

.my-nickname {
  font-size: 3vw !important;
}

.name-area {
  padding: 10px;
  text-align: left;
}

.icon-area {
  display: flex;
  width: 100%;
  justify-content: left;
}

.portfolio-icon {
  font-size: 1.8em;
  margin: 5px;
}

.mypic-area {
  position: absolute;
  top: 10vh;
  left: 30vw;
  width: 30vw;
  z-index: -1;
}

.mypic {
  width: 35vw;
}

.skill-card {
  position: absolute;
  right: 5vw;
  width: auto;
  max-width: 60%;
  background-color: rgba(255, 255, 255, 0.5);
}
